import React, { Component } from 'react';
import axios from 'axios';
import moment from 'moment';

const queueUrl = "https://devsupport.cebroker.com/queuescount";
const MASK = "hh:mm:ss A DD/MM/YY";
const QUEUE_DATA = "QUEUE_DATA";

let queues = {};

const styles = {
  table: {
    'overflowY': 'scroll'
  },
  tableHeader: {
    tr: { 'minWidth': '280px', 'fontWeight': 'bold' },
    td: { 'minWidth': '280px' },
    dates: {
      td: { 'minWidth': '110px', 'padding': '0 5px', 'width': '110px' }
    }
  }
}

class QueueList extends Component {

  constructor(props) {
    super(props);
    this.state = {
      dates: [],
      queues: {},
      loading: false,
      intervalSec: 30,
      loadingInterval: false,
      interval: null
    };
    this.loadData = this.loadData.bind(this);
    this.handleIntervalChange = this.handleIntervalChange.bind(this);
    this.loadDataInterval = this.loadDataInterval.bind(this);
    this.deleteHistory = this.deleteHistory.bind(this);
    this.loadDates = this.loadDates.bind(this);
  }

  getQueues(data) {
    let processedData = data.matchAll(/<tr style='color: (.+?);.+?>.+?<td>(.+?)<\/td>.+?<td>(.+?)<\/td>.+?<\/tr>/g);
    //let date = data.match(/<strong>(.+?)<\/strong>/i);
    //let pDate = new moment(date[1], "MM/DD/YYYY, HH:mm:SS A").toDate().getTime();
    let date = new Date();

    Array.from(processedData).forEach(match => {

      queues = this.state.queues;
      let history = queues[match[2]]?.history || [];

      let current = {
        value: match[3],
        color: match[1],
        date
      }

      let prev = history.length > 0 ? history[0] : null;

      if (prev) {
        current.diff = current.value - prev.value;
        if (prev.value > current.value) {
          current.valueShow = `${current.value} (-)`;
        } else if (prev.value < current.value) {
          current.valueShow = `${current.value} (+)`;
        } else {
          current.valueShow = `${current.value} (=)`;
        }
      } else {
        current.valueShow = current.value;
        current.diff = 'N/D';
      }

      history.unshift(
        current
      );

      this.loadDates(history);

      queues[match[2]] = {
        name: match[2],
        history
      };
      this.setState({ queues });
      localStorage.setItem(QUEUE_DATA, JSON.stringify(queues));
    });
  }

  loadDates(history) {
    let dates = history.map(q => {
      return q.date;
    });
    this.setState({ dates });
  }

  componentDidMount() {
    this.loadData();
  }

  handleIntervalChange = (event) => {
    this.setState({ intervalSec: event.target.value });
  }

  loadDataInterval = (event) => {
    let interval = this.state.interval;
    if (this.state.loadingSeq) {
      clearInterval(interval);
    } else {
      interval = setInterval(this.loadData, this.state.intervalSec * 1000);
      this.setState({ interval });
    }
    this.setState({ loadingSeq: !this.state.loadingSeq });
  }

  loadData = () => {
    this.setState({ loading: true });
    let storage = localStorage.getItem(QUEUE_DATA);
    if (storage) {
      this.setState({
        queues: JSON.parse(storage)
      }, () => {
        Object.keys(this.state.queues).forEach(queue => {
          this.loadDates(this.state.queues[queue].history);
        })
      })

    }
    axios.get(queueUrl)
      .then(res => {
        this.getQueues(res.data.item[0].text, this.state.queues);
        this.setState({ loading: false });
      }).catch(err => {
        console.error(err);
        this.setState({ loading: false });
      })
  }

  deleteHistory = () => {
    localStorage.removeItem(QUEUE_DATA);
    this.setState({
      queues: {}
    })
    alert("History deleted!");
  }

  render() {
    return (
      <div>
        {Object.keys(this.state.queues).length > 0 ?
          <table border='1' style={styles.table}>
            <thead>
              <tr style={styles.tableHeader.tr}>
                <td style={styles.tableHeader.td}>Queue</td>
                {
                  this.state.dates.map(dt => {
                    return <td key={dt} style={styles.tableHeader.dates.td}>{moment(dt).format(MASK)}</td>
                  })
                }
              </tr>
            </thead>
            <tbody>
              {
                Object.keys(this.state.queues).map(queue => {
                  return (
                    <tr key={queue}>
                      <td key={queue}>{queue}</td>
                      {
                        this.state.queues[queue].history.map(history => {
                          return <td style={{ color: history.color }} key={new Date().getTime() + "-" + history.date} title={history.diff}>{history.valueShow}</td>
                        })
                      }
                    </tr>
                  );
                })
              }
            </tbody>
          </table>
          :
          <p>No data to display!!!</p>
        }
        <br />
        <button onClick={this.loadData} disabled={this.state.loading}>{this.state.loading ? "Loading Queues..." : "Load Queues"}</button>
        <br />
        <label>
          Interval (Sec.):
          <input type="number" value={this.state.intervalSec} onChange={this.handleIntervalChange} />
        </label>
        <button onClick={this.loadDataInterval}>{this.state.loadingSeq ? "Stop loading Interval" : "Load Interval"}</button>
        <br />
        <button onClick={this.deleteHistory}>Delete History</button>
        <br />
      </div>
    );
  }
}

export default QueueList;